"use client";

import { signIn, signOut, useSession } from "next-auth/react";
import { useRouter } from "next/navigation";

export default function Navbar() {
  const { data: session } = useSession();
  const router = useRouter();
  const user = session?.user;
  const role = session?.user?.role; // Giả sử role có trong session

  return (
    <nav className="bg-gray-900 text-white px-6 py-3 flex justify-between items-center shadow-lg">
      {/* Logo */}
      <div className="flex items-center space-x-2 cursor-pointer" onClick={() => router.push("/")}>
        <svg className="w-8 h-8 text-orange-500" fill="currentColor" viewBox="0 0 20 20">
          <path d="M10 2a8 8 0 1 1 0 16A8 8 0 0 1 10 2zm0 1.5a6.5 6.5 0 1 0 0 13 6.5 6.5 0 0 0 0-13zM9 5h2v6H9V5zm0 7h2v2H9v-2z"/>
        </svg>
        <span className="text-xl font-semibold">My App</span>
      </div>

      {/* Menu */}
      <div className="flex space-x-6">
        <a href="/" className="hover:text-orange-400 transition">Home</a>
        {user && <a href="/dashboard" className="hover:text-orange-400 transition">Dashboard</a>}
        {user && role === "admin" && <a href="/admin" className="hover:text-orange-400 transition">Admin</a>}
      </div>

      {/* User Section */}
      <div className="flex items-center space-x-4">
        {!user ? (
          <button
            onClick={() => signIn("google")}
            className="bg-orange-500 px-4 py-2 rounded-lg hover:bg-orange-600 transition"
          >
            Đăng nhập
          </button>
        ) : (
          <div className="flex items-center space-x-3">
            <img src={user.image || ""} alt="Avatar" className="w-10 h-10 rounded-full border-2 border-orange-500" />
            <span className="hidden sm:block">{user.name}</span>
            <button
              onClick={() => signOut()}
              className="bg-red-500 px-3 py-1 rounded-lg hover:bg-red-600 transition"
            >
              Đăng xuất
            </button>
          </div>
        )}
      </div>
    </nav>
  );
}
